import React from 'react';

export default function getHeadElems(tabTitle: string, title: string, desc?: string, image?: string) {
	let imageurl = window.location.protocol + '//' + window.location.host + '/inkfarer.com/img/' + image;

	return [
		<title>{tabTitle === '' ? 'Inkfarer' : `${tabTitle} - Inkfarer`}</title>,
		<meta name="title" content={title} />,
		<meta name="description" content={desc === undefined ? '' : desc} />,

		<meta property="og:url" content={window.location.href} />,
		<meta property="og:title" content={title} />,
		<meta property="og:description" content={desc === undefined ? '' : desc} />,
		<meta property="og:image" content={image === undefined ? '' : imageurl} />,

		<meta property="twitter:card" content="summary_large_image" />,
		<meta property="twitter:url" content={window.location.href} />,
		<meta property="twitter:title" content={title} />,
		<meta property="twitter:description" content={desc === undefined ? '' : desc} />,
		<meta property="twitter:image" content={image === undefined ? '' : imageurl} />
	];
}