import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Helmet from 'react-helmet';
import getHeadElems from './getHeadElems';

export default function About() {
	return (
		<Row>
			<Helmet>
				{getHeadElems('About', 'About Inkfarer')}
			</Helmet>
			<Col xs={12} md={{span: 8, offset: 2}}>
				<div className="text-left">
					<h2 className="font-weight-bold">About me</h2>
					<p>
						I am Inkfarer (Formely known as randomoink).<br />
						I've been building stream overlays and running streams for various online communities since 2018.
						I additionally spend my free time learning Japanese, listening to music and playing games.<br />
						I'm currently studying software development.
					</p>
					<h2 className="font-weight-bold">Social Media</h2>
					<p>
						<span className="font-weight-bold">Discord: </span>inkfarer#6942<br />
						<span className="font-weight-bold">Twitter: </span><a href="https://twitter.com/inkfarer">@inkfarer</a><br />
						<span className="font-weight-bold">Twitch: </span><a href="https://www.twitch.tv/inkfarer">inkfarer</a><br />
						<span className="font-weight-bold">GitHub:  </span><a href="https://github.com/inkfarer">inkfarer</a><br />
						<span className="font-weight-bold">Email (checked infrequently): </span>inkfarer@inkfarer.com
					</p>
					<h2 className="font-weight-bold">Tools</h2>
					<p>
						My stream overlays use <a href="https://nodecg.com/">NodeCG</a> as an underlying framework and <a href="https://greensock.com/gsap/">GSAP</a> for animation.<br />
						They run on HTML, CSS and JavaScript.
					</p>
				</div>
			</Col>
		</Row>
	);
}