import React from 'react';
import {
	useParams
} from "react-router-dom";
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Helmet from 'react-helmet';
import * as projectElems from './projects.json';
import Carousel from 'react-bootstrap/Carousel';
import Err from './err';
import getHeadElems from './getHeadElems';

export default function Project() {
	let {projectId} = useParams();
	let projectElem = projectElems.projects.filter(elem => elem.id === projectId)[0];

	if (projectElem === undefined) {
		return <Err msg="Page not found!" />
	}

	let slides = [];
	for (let i = 0; i < projectElem.images.length; i++) {
		const element = projectElem.images[i];
		slides.push(
			<Carousel.Item>
				<img className="d-block w-100" src={`/inkfarer.com/img/${element}`} alt={`Slide ${i+1} for ${projectElem.name}`} />
			</Carousel.Item>
		);
	}

	return (
		<Row>
			<Helmet>
				{getHeadElems(projectElem.name, 'Inkfarer\'s website', projectElem.rawDescription, projectElem.images[0])}
			</Helmet>
			<Col xs={12} md={{span: 10, offset: 1}}>
				<Carousel>
					{slides}
				</Carousel>
				<div className="text-center font-weight-bold">{projectElem.name}</div>
				<div className="text-center" dangerouslySetInnerHTML={{__html: projectElem.description}}></div>
			</Col>
		</Row>
	);
}