import React from 'react';
import * as projectElems from './projects.json';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {Link} from "react-router-dom";
import getHeadElems from './getHeadElems';
import Helmet from 'react-helmet';

export default function Projects() {
	let projects : any = [];

	for (let i = 0; i < projectElems.projects.length; i++) {
		const element = projectElems.projects[i];
		projects.push(
		<Col key={element.id} md={6} lg={3} className="mb-4">
			<div className="image-box">
				<Link to={'/details/' + element.id}>
					<img className="image-box-image card-img-top" src={'/inkfarer.com/img/' + element.images[0]} alt={'Thumbnail for ' + element.name} />
					<p className="image-box-description">{element.name}</p>
				</Link>
			</div>
		</Col>
		);
	}

	return (
		<Row>
			<Helmet>
				{getHeadElems("", 'Inkfarer\'s website')}
			</Helmet>
			{projects}
		</Row>
	);
}